import { CustomSelect } from '@/components/dropDown/Dropdown';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { useState } from 'react';
import Register from './Register';
import CircularProgress from '@/components/circles/CircularProgress';

const rentData = [
    {
        text: 'محمد أحمد',
        text1: 'الى 2025/02/20 ',
        text2: 'كاش',
        text3: '900 ر.س',
        icon: <CircularProgress percentage={60} size={48} />,
    },
];
const rentColumns = [
    { text: 'المستأجر' },
    { text: 'المدة' },
    { text: 'طريقة الدفع' },
    { text: 'قيمة الإيجار' },
    { text: 'التقييم' },
];
const finData = [
    {
        text: '6,000 ر.س',
        text1: '2,000 ر.س',
        text2: '500 ر.س',
        text3: '900 ر.س',
        text4: '150 ر.س',
        icon: (
            <div className="h-6 w-16 bg-white-blue-50 rounded-sm p-1 border border-[#22AD5C] font-medium text-xs leading-[13.8px] tracking-normal text-[#22AD5C]">
                2,450 ر.س
            </div>
        ),
    },
];

const finColumns = [
    { text: 'إجمالي الايجار' },
    { text: 'تكاليف التشغيل' },
    { text: 'الصيانة الدورية' },
    { text: 'الأعطال' },
    { text: 'الإهلاك' },
    { text: 'الربح' },
];
const maintainceData = [
    {
        text: 'قيد التصليح',
        text1: '15/2/2025',
        text2: '15/2/2025',
        text3: '3',
        text4: 'التوكيل',
    },
];

const maintainceColumns = [
    { text: 'الحالة' },
    { text: 'تاريخ بدء الصيانة' },
    { text: 'مدة الصيانة' },
    { text: 'عدد الاعطال' },
    { text: 'مكان الصيانة' },
];

const Registers = () => {
    const [selectedValue, setSelectedValue] = useState('');

    return (
        <div className="mb-6">
            <div className="border border-[#E8E7EA]">
                <div className="flex flex-row items-center justify-between p-2">
                    <UnderlineHeading text="سجل التأجيرات" />
                    <CustomSelect
                        options={[
                            { label: 'Toyota', value: 'Toyota' },
                            { label: 'Nissan', value: 'Nissan' },
                            { label: 'Lexes', value: 'Lexes' },
                        ]}
                        value={selectedValue}
                        onValueChange={setSelectedValue}
                        placeholder="القائمة"
                        width="min-w-[90px]"
                        className="h-auto leading-5"
                    />
                </div>
                <Register data={rentData} columns={rentColumns} />
            </div>
            <div className="border border-[#E8E7EA] mt-4">
                <div className="flex flex-row items-center justify-between p-2">
                    <UnderlineHeading text="السجل المالي" />
                </div>
                <Register data={finData} columns={finColumns} />
            </div>
            <div className="border border-[#E8E7EA] mt-4">
                <div className="flex flex-row items-center justify-between p-2">
                    <UnderlineHeading text="سجل الصيانة" />
                    <CustomSelect
                        options={[
                            { label: 'Toyota', value: 'Toyota' },
                            { label: 'Nissan', value: 'Nissan' },
                            { label: 'Lexes', value: 'Lexes' },
                        ]}
                        value={selectedValue}
                        onValueChange={setSelectedValue}
                        placeholder="القائمة"
                        width="min-w-[90px]"
                        className="h-auto leading-5"
                    />
                </div>
                <Register data={maintainceData} columns={maintainceColumns} />
            </div>
        </div>
    );
};

export default Registers;
