import UnderlineHeading from '@/components/heading/UnderlineHeading';
import { Button } from '@/components/ui/button';
import { Car } from '@/types/cars.types';

interface Props {
    car: Car;
}

const CarInfo = ({ car }: Props) => {
    return (
        <div className="flex flex-col md:flex-row items-center gap-6 mb-6">
            <div className="flex-1">
                <img src={car.media.image} alt="" className="mb-4" />
                <div className="flex flex-row gap-4">
                    {car.media?.images?.map((el, index) => (
                        <img
                            key={index}
                            className="w-[123px] h-[80px] object-contain rounded-lg bg-white-blue-100 border border-primery-blue-200 shadow-[0px_2px_12px_0px_#14142B14]"
                            src={el}
                            alt=""
                        />
                    ))}
                </div>
            </div>
            <div className="flex-1 rounded-lg border border-[#E8E7EA]">
                <div className="flex flex-row items-center justify-between p-2">
                    <UnderlineHeading text="بيانات السيارة" />
                    <div className="flex flex-row items-center gap-2">
                        <p className="font-medium text-primery-blue-900 text-[18px] leading-[33.73px] tracking-normal text-start">
                            رقم التسجيل
                        </p>
                        <Button className="rounded-lg font-medium tracking-normal shadow-none text-[#9E939A] text-base leading-5 text-start p-2 border border-[#E1DEE0] bg-[#F5F4F5]">
                            {car.englishOtp.split('').join(' ')} {car.numericOtp}
                        </Button>
                    </div>
                </div>
                <div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            الماركة والموديل
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">
                            <img
                                className="w-10 h-10 object-contain"
                                src={`https://rent.ammarelgendy.com/${car.brand.logo}`}
                                alt=""
                            />
                            {`${car.brand.name} ${car.brand_model.name} ${car.make_year}`}
                        </p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            نوع ناقل الحركة
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.transmission_type}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            سعة المحرك
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.brand_model.engine_size}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            عداد المسافة الحالي
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.mileage} كيلومتر</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            اللون
                        </p>
                        <div className="flex flex-row items-center gap-4 p-2 w-full">
                            <span
                                className="w-[30px] h-[30px] rounded-full"
                                style={{ backgroundColor: car.color }}
                            ></span>
                            {car.color}
                        </div>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            نوع السيارة
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.brand_model.type}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            رقم الهيكل
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">{car.vin}</p>
                    </div>
                    <div className="flex flex-row items-center border-b border-b-white-blue-500">
                        <p className="bg-white-blue-100 px-4 py-4 text-sm text-primery-blue-500 font-semibold tracking-normal leading-6 w-1/2">
                            الحالة الحالية
                        </p>
                        <p className="flex flex-row items-center gap-4 p-2 w-full">
                            <span className="px-3 py-[6px] bg-[#F59E0B] rounded text-base text-white-blue-50 font-medium tracking-normal leading-5">
                                {car.status}
                            </span>
                        </p>
                    </div>
                </div>
            </div>
        </div>
    );
};

export default CarInfo;
