import { CustomSelect } from '@/components/dropDown/Dropdown';
import UnderlineHeading from '@/components/heading/UnderlineHeading';
import React, { useState } from 'react';
import SettingsIcon from '@/components/icons/statistics/SettingIcon';
import UserIcon from '@/components/icons/statistics/UserIcon';
import CalendarIcon from '@/components/icons/statistics/CalendarIcon';
import CarIcon from '@/components/icons/statistics/CarIcon';
import { Card } from '@/types/statistics';
import StatisticsCards from '@/components/cards/StatisticsCards';

const cards: Card[] = [
    { text: 'المسافة المقطوعة', label: 'كيلومتر', icon: <CarIcon />, num: 1500, percent: 12 },
    { text: 'متوسط السرعة', label: 'كم/س', icon: <CalendarIcon />, num: 120, percent: 0 },
    { text: 'استهلاك الوقود', label: 'لتر', icon: <UserIcon />, num: 80, percent: -0.3 },
    { text: 'مخالفات مسجلة', label: '', icon: <SettingsIcon />, num: 0, percent: 0.3, negative: true },
];

const ActualPerformance = () => {
    const [selectedValue, setSelectedValue] = useState('');

    return (
        <div className="mb-6">
            <div className="flex flex-row items-center justify-between mb-4">
                <UnderlineHeading text="الأداء الفعلي" />
                <CustomSelect
                    options={[
                        { label: 'Toyota', value: 'Toyota' },
                        { label: 'Nissan', value: 'Nissan' },
                        { label: 'Lexes', value: 'Lexes' },
                    ]}
                    value={selectedValue}
                    onValueChange={setSelectedValue}
                    placeholder="الحجز الحالي"
                    width="min-w-[90px]"
                    className="h-auto leading-5"
                />
            </div>
            <StatisticsCards cards={cards} />
        </div>
    );
};

export default ActualPerformance;
