import CircularProgress from '@/components/circles/CircularProgress';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from '@/components/ui/table';

interface Props {
    className?: string;
    data: Array<Record<string, unknown>>;
    columns: Array<Record<string, unknown>>;
}

const Register = ({ className, data, columns }: Props) => {
    return (
        <Table className={className}>
            <TableHeader className="border-b border-[#E8E7EA]">
                <TableRow>
                    {columns.map((col, index) => (
                        <TableHead
                            className="px-4 py-3 bg-white-blue-100 text-sm tracking-normal font-semibold leading-[26.24px] text-primery-blue-500"
                            key={index}
                        >
                            {col?.text as string}
                        </TableHead>
                    ))}
                </TableRow>
            </TableHeader>
            <TableBody>
                {data.map((el, index) => (
                    <TableRow key={index}>
                        {Object.keys(el).map((key, idx) => (
                            <TableCell className="p-4" key={idx}>
                                {el[key] as React.ReactNode}
                            </TableCell>
                        ))}
                    </TableRow>
                ))}
            </TableBody>
        </Table>
    );
};

export default Register;
