import { useParams } from 'react-router-dom';
import Heading from '../components/carView/Heading';
import CarInfo from '../components/carView/CarInfo';
import ActualPerformance from '../components/carView/ActualPerformance';
import Registers from '../components/carView/Registers';
import { SubtleButton } from '@/components/buttons/SubtleButton';
import { SecondaryButton } from '@/components/buttons/SecondaryButton';
import { PrimaryButton } from '@/components/buttons/PrimaryButton';
import { useEffect, useState } from 'react';
import { apiGet } from '@/lib/axios/axios-utils';
import { endpoints } from '@/lib/axios/axios-global';
import { LoadingSpinner } from '@/components/Loading/LoadingSpinner';
import { Car } from '@/types/cars.types';

const CarDetailsPage = () => {
    const params = useParams();
    const { id } = params;
    const [car, setCar] = useState<Car | null>(null);
    const [loading, setLoading] = useState(true);
    const [error, setError] = useState<string | null>(null);

    useEffect(() => {
        const fetchCar = async () => {
            try {
                const response = await apiGet({
                    endpoint: endpoints.cars.show + id,
                });
                setCar(response.data.data);
            } catch (error) {
                setError('Failed to load car details');
            } finally {
                setLoading(false);
            }
        };

        if (id) {
            fetchCar();
        } else {
            setError('Invalid car ID');
            setLoading(false);
        }
    }, [id]);

    if (loading) {
        return <LoadingSpinner />;
    }

    if (error) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl text-red-500">{error}</h2>
            </div>
        );
    }

    if (!car) {
        return (
            <div className="mt-6 mb-[102px] text-center py-20">
                <h2 className="text-xl">Car not found</h2>
            </div>
        );
    }

    return (
        <div className="mt-6 mb-[102px]">
            <Heading car={car} />
            <CarInfo car={car} />
            <ActualPerformance />
            <Registers />
            <div className="flex flex-row justify-between items-center">
                <SubtleButton type="button" className="xl:px-[61.5px] xl:py-4 xl:font-bold">
                    رجوع
                </SubtleButton>
                <div>
                    <SecondaryButton type="button" className="xl:px-[71.5px] xl:py-[18px] xl:font-bold">
                        تعديل
                    </SecondaryButton>
                    <PrimaryButton className="xl:px-[154px] xl:py-[18px] xl:font-bold ms-6">حجز</PrimaryButton>
                </div>
            </div>
        </div>
    );
};

export default CarDetailsPage;
