import UnderlineHeading from '@/components/heading/UnderlineHeading';
import LicensePlateIcon from '@/components/icons/car/CarNumberIcon';
import { Car } from '@/types/cars.types';

interface Props {
    car: Car;
}

const Heading = ({ car }: Props) => {
    return (
        <div className="flex flex-row items-center justify-between mb-6 p-2">
            <UnderlineHeading
                text={`${car.brand.name} ${car.brand_model.name} ${car.make_year}`}
                className=" text-primery-blue-800 leading-[60px] text-[32px]"
            />
            <div className="flex flex-row items-center gap-2 justify-between p-2 rounded-lg border border-primery-blue-300">
                <p className="font-bold text-[22px] text-white-blue-900 leading-[33px] tracking-normal text-start">
                    {car.arabicOtp.split('').join(' ')} {car.numericOtp}
                </p>
                <LicensePlateIcon />
                <p className="font-bold text-[22px] text-white-blue-900 leading-[33px] tracking-normal text-start">
                    {car.englishOtp.split('').join(' ')} {car.numericOtp}
                </p>
            </div>
        </div>
    );
};

export default Heading;
